import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router'
import styles from './NFTCard.module.css'
import NavigationBar from '../Navbar/NavigationBar'
import { useHistory } from "react-router-dom";

export default function NFTCard() {

    const history = useHistory()

    const { id, token } = useParams()

    const [cardInfo, setCardInfo] = useState([])
    const [event, setEvent] = useState([])
    const [info, setInfo] = useState({
        user: '',
        img: '',
        name: '',
    })

    const [traits, setTraits] = useState([])
    // const [loading, setLoading] = useState(true)
    const [loaded, setLoaded] = useState(false);


    const options = {
        method: 'GET',
        headers: { Accept: 'application/json', 'X-API-KEY': '087158b0d16f4e73a708168cefa9123c' }
    };

    const api = `https://kaiscorsproxy.herokuapp.com/https://api.opensea.io/api/v1/asset/${id}/${token}`
    const api2 = `https://kaiscorsproxy.herokuapp.com/https://api.opensea.io/api/v1/events?asset_contract_address=${id}&token_id=${token}&only_opensea=false`

    const fetchData = async () => {
        const data = await fetch(api, options)
        const response = await data.json()
        if (data.status !== 404) {
            setCardInfo(response)
            setInfo({
                user: response.owner.user,
                img: response.owner,
                name: response.collection
            })
            setTraits(response.traits)
            // setLoading(false)
        } else {
            history.push('/404')
        }
    }


    const fetchEvent = async () => {
        const data2 = await fetch(api2, options)
        const response = await data2.json()
        setEvent(response.asset_events)
    }


    useEffect(() => {
        fetchData()
        fetchEvent()
    }, [setCardInfo])


    let bids = []
    event ?? event.forEach(item => {
        bids.push(item.bid_amount)
    })
    let highestBid = ((Math.max(...bids) / 1000000000000000000).toString().slice(0, 5))



    const { image_original_url } = cardInfo && cardInfo
    const { profile_img_url } = info.img && info.img
    const { name } = info.name && info.name
    const { username } = info.user !== null ? info.user : 'not found'


    let imageDisplay = (
        <div>
            {loaded ? null : (
                <div className={styles.spinnerWrapper}>
                    <div style={{ width: '150px', height: '150px' }} className="spinner-border" role="status" >
                        <span className='sr-only'>Loading...</span>
                    </div>
                </div>
            )
            }
            <div>
                <img
                    className={styles.mainImage}
                    style={loaded ? {} : { display: 'none' }}
                    src={image_original_url}
                    onLoad={() => setLoaded(true)}
                    alt=""
                />
            </div>

        </div >
    );



    let infoDisplay = (
        <div>
            <div className={styles.topSection}>
                <div>
                    <h1 className="fw-bold fs-1">{name}</h1>
                    <div className="fw-bold fs-2 mb-3">#{token}</div>
                </div>
                <div>
                    <div>Highest Bid</div>
                    <div style={{ display: 'flex', alignItems: 'center' }}>
                        <img src="/nft-spotter/imgs/eth.svg" style={{ width: '25px', height: '25px' }} alt="" />
                        {highestBid}
                    </div>
                </div>
            </div>

            <div className={styles.owner}>
                <img src={profile_img_url} alt="" />
                <div>Owner: {username}</div>
            </div>


        </div>
    )



    let traitsDisplay = traits.map((trait, index) => {
        let { trait_type, value } = trait
        return (
            <div key={index} className={`${styles.traitsDiv}`}>
                <div className="traitType">{trait_type}:</div>
                <div className={styles.value}>{value}</div>
            </div>
        )
    })


    return (
        <>
            <NavigationBar />
            <div className={styles.myContainer}>
                {imageDisplay}
                <div className={styles.rightCol}>
                    {infoDisplay}
                    <div className={styles.traitsContainer}>
                        {traitsDisplay}
                    </div>
                </div>
            </div>
        </>
    )
}
